import React, { useState } from 'react';
import { useQuery, useMutation, gql } from '@apollo/client';

const GET_TRANSACTIONS = gql`
  query GetTransactions {
    allTransactions {
      id
      date
      montant
      type
      compte {
        id
        solde
      }
    }
  }
`;

const SAVE_TRANSACTION = gql`
  mutation SaveTransaction($transaction: TransactionInput!) {
    saveTransaction(transaction: $transaction) {
      id
      date
      type
      montant
      compte {
        id
        solde
        dateCreation
        type
      }
    }
  }
`;

function Transactions() {
  const { loading, error, data } = useQuery(GET_TRANSACTIONS);
  const [saveTransaction] = useMutation(SAVE_TRANSACTION, {
    refetchQueries: [{ query: GET_TRANSACTIONS }]
  });

  const [newTransaction, setNewTransaction] = useState({
    date: '',
    type: 'DEPOT',
    montant: '',
    compteId: ''
  });

  const handleAddTransaction = async (e) => {
    e.preventDefault();
    try {
      if (!newTransaction.date || !newTransaction.montant || !newTransaction.compteId) {
        alert('Please fill in all fields.');
        return;
      }

      await saveTransaction({
        variables: {
          transaction: {
            date: newTransaction.date || new Date().toISOString(),
            type: newTransaction.type,
            montant: parseFloat(newTransaction.montant),
            compteId: newTransaction.compteId,
          },
        },
      });

      // Reset form
      setNewTransaction({ 
        date: '', 
        type: 'DEPOT', 
        montant: '', 
        compteId: '' 
      });
    } catch (err) {
      alert('Failed to save transaction: ' + err.message);
    }
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error.message}</p>;

  return (
    <div>
      <h2>Transactions</h2>
      
      <form onSubmit={handleAddTransaction}>
        <input
          type="date"
          value={newTransaction.date}
          onChange={(e) => setNewTransaction({ ...newTransaction, date: e.target.value })}
          required
        />
        
        <select
          value={newTransaction.type}
          onChange={(e) => setNewTransaction({ ...newTransaction, type: e.target.value })}
        >
          <option value="DEPOT">DEPOT</option>
          <option value="RETRAIT">RETRAIT</option>
        </select>
        
        <input
          type="number"
          placeholder="Amount"
          value={newTransaction.montant}
          onChange={(e) => setNewTransaction({ ...newTransaction, montant: e.target.value })}
          required
        />
        
        <input
          type="text"
          placeholder="Account ID"
          value={newTransaction.compteId}
          onChange={(e) => setNewTransaction({ ...newTransaction, compteId: e.target.value })}
          required
        />
        
        <button type="submit">Add Transaction</button>
      </form>

      <div>
        <h3>Existing Transactions</h3>
        {data.allTransactions.map((transaction) => (
          <div key={transaction.id}>
            {transaction.type}: {transaction.montant} on {transaction.date} (Account: {transaction.compte.id})
          </div>
        ))}
      </div>
    </div>
  );
}

export default Transactions;